import { JwtHelperService } from '@auth0/angular-jwt';
import { Component, OnInit } from '@angular/core';
import { MatSidenav, MatButtonModule } from '@angular/material';
import { User } from 'src/app/clases/User';
import { AuthService } from 'src/app/servicios/auth.service';
import { Router } from '@angular/router';
import { Empleado } from 'src/app/clases/Empleado';
import { Cliente } from 'src/app/clases/Cliente';
import {EmpleadoService}from 'src/app/servicios/empleado.service';

@Component({
  selector: 'app-navegacion',
  templateUrl: './navegacion.component.html',
  styleUrls: ['./navegacion.component.scss']
})
export class NavegacionComponent implements OnInit {

   user: User;
   empleado:Empleado;
   cliente:Cliente;

   public isCliente:any=null;
   public userUid:string=null;

  constructor(private authService: AuthService, private router: Router,private empService:EmpleadoService) {
    this.getCurrentUser(); 
  }
 
  ngOnInit() {

    
    
  }

  getCurrentUser()
  {
     

            const data = localStorage.getItem('Empleado');
  
            if(data){
              this.empleado=JSON.parse(data);       
              this.user = new User(this.empleado.usuario,this.empleado.tipo,this.empleado.id,this.empleado.nombre);        
            }else{
              const data = localStorage.getItem('Cliente');

              this.cliente=JSON.parse(data);  

              this.cliente.tipo="Cliente";

              this.user = new User(this.cliente.email,this.cliente.tipo,this.cliente.uid,this.cliente.displayName);        

            }

  }

  logout() {
    localStorage.clear();
    this.authService.logout();
    this.router.navigate(['/Login']); 
  }


}
