import { EmpleadoService } from './../../../servicios/empleado.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Empleado } from 'src/app/clases/Empleado';

@Component({
  selector: 'app-empleados-modify',
  templateUrl: './empleados-modify.component.html',
  styleUrls: ['./empleados-modify.component.scss']
})
export class EmpleadosModifyComponent implements OnInit {
  @Input() showModal: boolean;
  @Output() closeModal: EventEmitter<void>;
  @Output() modificadoCorrectamente: EventEmitter<void>;
  id: string;
  usuario: string;
  nombre: string;
  tipo: string;
  form: FormGroup;
  errorMessage: string;
  error: boolean;
  empleado:Empleado;

  constructor(private fb: FormBuilder, private empleadoService: EmpleadoService) {
    this.form = this.fb.group({
      usuario: [''],
      nombre: [''],
      tipo: ['']
    });

    this.modificadoCorrectamente = new EventEmitter<void>();
    this.closeModal = new EventEmitter<void>();
  }

  ngOnInit() {
  }

  cargarModal(empleado:Empleado) {

    this.empleado=empleado;

    this.id = empleado.id;
    this.nombre = empleado.nombre;
    this.usuario = empleado.usuario;
    this.tipo = empleado.tipo;
    this.cargarForm();
  }

  private cargarForm() {
    this.form = this.fb.group({
      usuario: [this.usuario, Validators.required],
      nombre: [this.nombre, Validators.required],
      tipo: [this.tipo]
    });
  }

  Restablecer() {
    this.cargarForm();
  }

  Submit() {
    this.errorMessage = '';
    this.error = false;
    if (this.form.valid) {
      
      this.empleado.usuario = this.form.get('usuario').value;
      this.empleado.nombre = this.form.get('nombre').value;
      this.empleado.tipo = this.form.get('tipo').value;

      this.empleadoService.Modificar(this.empleado)
        .then(
          response => {
            console.log(response);
            if (response) {
              this.modificadoCorrectamente.emit();
              this.closeModal.emit();
            } else {
              this.error = true;
              this.errorMessage = 'Error al modificar';
            }
          }
        )
        .catch(
          error => {
            this.error = true;
            this.errorMessage = 'Error al modificar';
            console.log(error);
          }
        );
    } else {
      this.errorMessage = 'Debe completar los campos correctamente.';
      this.error = true;
    }
  }

  cerrar() {
    this.closeModal.emit();
    this.form.reset();
  }
}
